.navbar{

height: 5rem;
width: 100vw;
margin: 0 auto; /*top-bottom left-right*/
padding: 1rem 2rem;
display: flex;
align-items: center;
justify-content:space-between;
position: sticky;
top: 0;
z-index: 3;
max-width: 75rem;

}

.logo{
    object-fit: cover;
    height: 8rem;
    width: 8rem; /*relative to root font size*/


}

.desktopMenuListItem{
    margin: 1rem;
    cursor: pointer;

}
.desktopMenuListItem:hover{
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;

}
.desktopMenuBtn{
    border: none;
    background-color: white;
    color: black;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    border-radius: 2rem;
    cursor: pointer;
}
.desktopMenuImg{
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    margin: 1rem;
    
}
.active{
    color: yellow;
    padding-bottom: 3px;
    border-bottom: 3px solid yellow;
}

.NavMenu{
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background-color: rgb(40, 40, 40);
    border-radius: 1rem;
}

.ListItem{
    color: white;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    background-color: rgb(30, 30, 30);
}
.mobMenu{
    display: none;
    object-fit: cover;
    height: 1.8rem;
}
@media screen and (max-width:970px){
    .mobMenu{
        display: flex;
    }
    .desktopMenu{
        display: none;
    }
    .desktopMenuBtn{
        display: none;
    }
}